import { Config, ExpDataFetcher } from '@abetterchoice/abc_web_core';
import { getTtDeviceId, postTtReq } from '../cc_tt_utils';
import { fetchTtMpNetwork, ttNetworkRequest } from '../ttNetworkRequest';

/**
 * @file cc_tt_exp_fetcher.ts
 * @author fitcher
 * @date 2024-09-29
 */
export class CcTtExpDataFetcher extends ExpDataFetcher {
  constructor(config: Config) {
    super(config);
  }
  // 通过json请求
  public sendStringRequestWithStringResponse(
    url: string,
    reqData: Record<string, unknown>,
    headers: Record<string, string> = {},
    timeout: number,
  ): Promise<string> {
    return postTtReq(url, reqData, headers, timeout);
  }

  // 通过PB二进制流请求
  public sendBytesRequestsWithBytesResponse(
    url: string,
    reqData: object,
    headers?: {},
    timeout?: number,
  ) {
    const fetchNetworkRequest: ttNetworkRequest = {
      url,
      data: reqData,
      headers,
      timeout,
    };
    return fetchTtMpNetwork(fetchNetworkRequest);
  }

  protected getDeviceId(prefix: string) {
    return getTtDeviceId(prefix);
  }
}
