import {
  Config,
  EVENT_SDK_PLATFORM_DEFAULT_VAL,
  EVENT_SDK_VERSION_DEFAULT_VAL,
  ExpComponent,
} from '@abetterchoice/abc_web_core';
import { CcTtExpDataStorage } from './cc_tt_storage';
import { CcTtExpDataFetcher } from './cc_tt_exp_fetcher';
import { CcTtExpReporter } from './cc_tt_exp_reporter';

/**
 * @file cc_tt_exp_component.ts
 * @author fitcher
 * @date 2024-09-29
 */

// @ts-ignore
// eslint-disable-next-line no-undef
const SDK_VERSION = TABSDK_VERSION || EVENT_SDK_VERSION_DEFAULT_VAL; // 编译时获取
// @ts-ignore
// eslint-disable-next-line no-undef
const SDK_PLATFORM = TABSDK_PLATFORM || EVENT_SDK_PLATFORM_DEFAULT_VAL;

export class CcTtExpComponent extends ExpComponent {
  constructor(config: Config) {
    super(config);
  }

  protected initExpFetcher(): any {
    this.expDataFetcher = new CcTtExpDataFetcher(this.config);
  }

  protected initExpReport(): any {
    this.expDataReporter = new CcTtExpReporter(this.config, SDK_VERSION, SDK_PLATFORM);
  }

  protected initExpStorage(): any {
    this.expDataStorage = new CcTtExpDataStorage(this.config);
  }
}

