import {
  Config,
  DefaultLogger,
  EVENT_PROPERTIES_DEVICE_MODEL,
  EVENT_PROPERTIES_MANUFACTURER,
  EVENT_PROPERTIES_MG_PLATFORM,
  EVENT_PROPERTIES_MG_VERSION, EVENT_PROPERTIES_NETWORK_TYPE,
  // EVENT_PROPERTIES_NETWORK_TYPE,
  EVENT_PROPERTIES_OS,
  EVENT_PROPERTIES_OS_VERSION,
  EVENT_PROPERTIES_SCENE,
  EVENT_PROPERTIES_SCREEN_HEIGHT,
  EVENT_PROPERTIES_SCREEN_WIDTH,
  EVENT_PROPERTIES_SYSTEM_LANGUAGE,
  EventStorage,
} from '@abetterchoice/abc_web_core';
import { isByteDancePlatform, isNativePlatform } from '../cc_tt_utils';

export class CcTtEventStorage extends EventStorage {
  constructor(config: Config, sdkVersion: string, sdkPlatform: string) {
    super(config, sdkVersion, sdkPlatform);
  }

  public async initSystemPropertiesMap(): Promise<Map<string, any>> {
    const propertiesMap = new Map();
    if (isNativePlatform() || !isByteDancePlatform()) {
      DefaultLogger.error('coding platform not supported');
      return propertiesMap;
    }
    // @ts-ignore
    // eslint-disable-next-line no-undef
    const deviceInfo = tt.getSystemInfoSync();
    console.log(deviceInfo);
    propertiesMap.set(EVENT_PROPERTIES_DEVICE_MODEL, deviceInfo.model);
    propertiesMap.set(EVENT_PROPERTIES_SCREEN_WIDTH, deviceInfo.screenWidth);
    propertiesMap.set(EVENT_PROPERTIES_SCREEN_HEIGHT, deviceInfo.screenHeight);
    propertiesMap.set(EVENT_PROPERTIES_MANUFACTURER, deviceInfo.brand);
    propertiesMap.set(EVENT_PROPERTIES_OS, deviceInfo.platform);
    propertiesMap.set(EVENT_PROPERTIES_OS_VERSION, deviceInfo.system);
    propertiesMap.set(EVENT_PROPERTIES_SYSTEM_LANGUAGE, deviceInfo.language);
    const networkType = await new Promise((resolve, reject) => {
      // @ts-ignore
      // eslint-disable-next-line no-undef
      tt.getNetworkType({
        success(res: { networkType: unknown; }) {
          resolve(res.networkType);
        },
        fail() {
          reject('unknown');
        },
      });
    });
    propertiesMap.set(EVENT_PROPERTIES_NETWORK_TYPE, networkType);
    // @ts-ignore
    // eslint-disable-next-line no-undef
    const launchOptions = tt.getLaunchOptionsSync();
    propertiesMap.set(EVENT_PROPERTIES_SCENE, String(launchOptions.scene));
    // @ts-ignore
    // eslint-disable-next-line no-undef
    const envInfo = tt.getEnvInfoSync();
    // 小游戏版本号（如果是预览版本的小游戏，该值为 preview ）
    propertiesMap.set(EVENT_PROPERTIES_MG_VERSION, envInfo.microapp.mpVersion);
    // 宿主平台名称，示例："Toutiao"/"Douyin"等
    propertiesMap.set(EVENT_PROPERTIES_MG_PLATFORM, deviceInfo.appName);
    DefaultLogger.debug('initSystemPropertiesMap', deviceInfo, launchOptions, envInfo);
    return propertiesMap;
  }

  getStorageString(storageKey: string) {
    if (isNativePlatform() || !isByteDancePlatform()) {
      DefaultLogger.error('coding platform not supported');
      return;
    }
    try {
      // @ts-ignore
      // eslint-disable-next-line no-undef
      return tt.getStorageSync(storageKey);
    } catch (e) {
      DefaultLogger.error(`getStorageData: storageKey: ${storageKey} is error`);
    }
  }
  putStorageString(storageKey: string, storageData: any) {
    if (isNativePlatform() || !isByteDancePlatform()) {
      DefaultLogger.error('coding platform not supported');
      return;
    }
    try {
      // @ts-ignore
      // eslint-disable-next-line no-undef
      return tt.setStorageSync(storageKey, storageData);
    } catch (e) {
      DefaultLogger.error(`setStorageData: storageKey: ${storageKey} : ${storageData} is error`);
    }
  }
  removeStorageString(storageKey: string) {
    if (isNativePlatform() || !isByteDancePlatform()) {
      DefaultLogger.error('coding platform not supported');
      return;
    }
    if (storageKey === undefined || storageKey.length === 0) {
      return;
    }
    try {
      // @ts-ignore
      // eslint-disable-next-line no-undef
      return tt.removeStorageSync(storageKey);
    } catch (e) {
      DefaultLogger.error(`removeStorageSync: storageKey: ${storageKey} is error`);
    }
  }

  getStorageAllKey() {
    if (isNativePlatform() || !isByteDancePlatform()) {
      DefaultLogger.error('coding platform not supported');
      return;
    }
    try {
      // @ts-ignore
      // eslint-disable-next-line no-undef
      const storageRes = tt.getStorageInfoSync();
      return storageRes.keys;
    } catch (e) {
      DefaultLogger.error(`getStorageAllKey: ${e}`);
    }
  }
}
