import { DefaultLogger } from '@abetterchoice/abc_web_core';
import { isArrayBuffer, isByteDancePlatform, isNativePlatform } from './cc_tt_utils';

/**
 * @file ttNetworkRequest.ts
 * @author fitcher
 * @date 2024-09-29
 */
export interface ttNetworkRequest {
  url: string;
  data: any;
  headers?: {};
  timeout?: number;
}

export function fetchTtMpNetwork(networkRequest: ttNetworkRequest) {
  if (isNativePlatform() || !isByteDancePlatform()) {
    return Promise.reject(new Error('fetchMpNetwork platform is not supported'));
  }
  let requestSetting = {
    headers: { 'Content-Type': 'application/json;charset=UTF-8' },
    method: 'POST',
    responseType: 'text',
  };
  if (isArrayBuffer(networkRequest.data.buffer)) {
    // 说明是PB请求
    const pbSetting = {
      headers: Object.assign(
        {},
        { 'Content-Type': 'application/proto' },
        networkRequest.headers,
      ),
      responseType: 'arraybuffer',
    };
    requestSetting = Object.assign({}, requestSetting, pbSetting);
  }
  const requestHeaders = Object.assign({}, requestSetting, networkRequest.headers);
  DefaultLogger.debug(requestSetting);
  return new Promise(((resolve, reject) => {
    // @ts-ignore
    // eslint-disable-next-line no-undef
    tt.request({
      url: networkRequest.url,
      data: networkRequest.data,
      method: 'POST',
      header: requestHeaders,
      responseType: 'text',
      timeout: networkRequest.timeout,
      success(res) {
        DefaultLogger.debug(res.data);
        if (res.statusCode === 200) {
          if (requestSetting.responseType === 'arraybuffer') {
            resolve(res.data);
          } else {
            resolve(JSON.stringify(res.data as string));
          }
        } else {
          reject(new Error('error network data'));
        }
      },
      fail() {
        DefaultLogger.debug('fail');
        reject();
      },
    });
  }));
}
