import { Config, FeatureFlagComponent } from '@abetterchoice/abc_web_core';
import { CcTtFeatureFlagStorage } from './cc_tt_feature_flag_storage';
import { CcTtFeatureFlagReporter } from './cc_tt_feature_flag_reporter';
import { CcTtFeatureFlagDataFetcher } from './cc_tt_feature_flag_fetcher';

export class CcTtFeatureFlagComponent extends FeatureFlagComponent {
  constructor(config: Config) {
    super(config);
  }

  protected initFeatureFlagFetcher() {
    this.featureFlagFetcher = new CcTtFeatureFlagDataFetcher(this.config);
  }

  protected initFeatureFlagReporter() {
    this.featureFlagReporter = new CcTtFeatureFlagReporter(this.config.gameId, this.config.unitId || '');
  }

  protected initFeatureFlagStorage() {
    this.featureFlagStorage = new CcTtFeatureFlagStorage(this.config);
  }
}
