import { Config, ExpReportFetcher } from '@abetterchoice/abc_web_core';
import { fetchTtMpNetwork, ttNetworkRequest } from '../ttNetworkRequest';
import { getTtDeviceId, postTtReq } from '../cc_tt_utils';

/**
 * @file cc_tt_reporter_fetcher.ts
 * @author fitcher
 * @date 2024-09-30
 */
export class CcTtExpReporterFetcher extends ExpReportFetcher {
  constructor(config: Config, sdkVersion: string, sdkPlatform: string) {
    super(config, sdkVersion, sdkPlatform);
  }

  protected sendBytesRequestWithBytesResponse(
    url: string,
    reqData: object,
    headers: {},
    timeout: number,
  ): any {
    const reportNetworkRequest: ttNetworkRequest = {
      url,
      data: reqData,
      headers,
      timeout,
    };
    return fetchTtMpNetwork(reportNetworkRequest);
  }

  protected sendStringRequestWithStringResponse(
    url: string,
    reqData: Record<string, unknown>,
    headers: Record<string, string> = {},
    timeout?: number,
  ) {
    return postTtReq(url, reqData, headers, timeout);
  }

  protected getDeviceId(prefix: string) {
    return getTtDeviceId(prefix);
  }
}
