import { Config, EventReporter } from '@abetterchoice/abc_web_core';
import { fetchTtMpNetwork, ttNetworkRequest } from '../ttNetworkRequest';
import { postTtReq } from '../cc_tt_utils';

/**
 * @file cc_tt_event_reporter.ts
 * @author fitcher
 * @date 2024-10-01
 */
export class CcTtEventReporter extends EventReporter {
  constructor(config: Config) {
    super(config);
  }

  public sendBytesRequestWithBytesResponse(url: string, reqData: object, headers?: {}, timeout?: number) {
    const fetchNetworkRequest: ttNetworkRequest = {
      url,
      data: reqData,
      headers,
      timeout,
    };
    return fetchTtMpNetwork(fetchNetworkRequest);
  }

  public sendStringRequestWithStringResponse(
    url: string,
    reqData: Record<string, unknown>,
    headers: Record<string, string> = {},
    timeout?: number,
  ) {
    return postTtReq(url, reqData, headers, timeout);
  }
}
