import { Config, ConfigInfo, DefaultLogger, ExperimentInfo } from '@abetterchoice/abc_web_core';
import { ABetterChoiceAPIForNative } from './ABetterChoiceAPI.cc';

class ABetterChoice {
  private static instanceMap = {};
  private static defaultInstance: ABetterChoiceAPIForNative;
  static shareInstance(gameId: string) {
    if (this.instanceMap[gameId] !== undefined) {
      return this.instanceMap[gameId];
    }
    if (this.defaultInstance !== undefined) {
      return this.defaultInstance;
    }
    return undefined;
  }

  /**
   * create a new instance
   * @param {Object} config
   * @param {String} config.gameId Project APP ID, require
   * @param {String} config.apiKey Project API keys, require
   * @param {String} config.serverUrl Project Server Url, option
   * @param {Object} config.autoTrack auto track app Events, option
   * @param {Boolean} config.autoTrack.mgShow Auto Track App Show Events, option
   * @param {Boolean} config.autoTrack.mgHide Auto Track App Hide Events, option
   * @param {Boolean} config.autoTrack.mgShare Auto Track App Share Events, option
   * @param {Boolean} config.enableLog Enable Log Printing, option
   */
  static async init(config: Config) {
    if (this.shareInstance(config?.gameId) != undefined) {
      DefaultLogger.debug('has initialed, ignore this init and return previous init sdk object');
      return this.shareInstance(config.gameId);
    }
    const instance = new ABetterChoiceAPIForNative(config);
    if (instance !== undefined) {
      this.defaultInstance = instance;
      this.instanceMap[config.gameId] = instance;
      return await instance.initSDK(config);
    }
    return false;
  }

  static login(userId: string, gameId = '') {
    this.shareInstance(gameId).login(userId);
  }

  static logout(gameId = '') {
    this.shareInstance(gameId).logout();
  }

  static setCommonProperties(properties = {}, gameId = '') {
    this.shareInstance(gameId).setCommonProperties(properties);
  }

  static getCommonProperties(gameId = '') {
    return this.shareInstance(gameId).getCommonProperties();
  }

  static clearCommonProperties(gameId = '') {
    this.shareInstance(gameId).clearCommonProperties();
  }

  static unsetCommonProperty(propertiesKey: string, gameId = '') {
    this.shareInstance(gameId).unsetCommonProperty(propertiesKey);
  }

  static getExperiment(layerCode: string, isNeedReport = undefined, gameId = ''): ExperimentInfo | undefined {
    return this.shareInstance(gameId).getExperiment(layerCode, isNeedReport);
  }

  static logExperimentExposure(expInfo: ExperimentInfo, gameId = ''): Promise<boolean> {
    return this.shareInstance(gameId).logExperimentExposure(expInfo);
  }

  static getConfig(configKey, gameId = ''): ConfigInfo | undefined {
    return this.shareInstance(gameId).getConfig(configKey);
  }


  /**
   * Track a normal Event.
   *  @param {String} eventCode event code
   * @param {Object} properties event properties
   * @param {String} gameId Project App ID
   */
  static track(eventCode: string, properties: {}, gameId: string = ''): Promise<boolean> {
    return this.shareInstance(gameId).track(eventCode, properties);
  }

  static setLogLevel(level: number): void {
    DefaultLogger.setLevel(level);
  }

  static getDeviceId(gameId = '') {
    return this.shareInstance(gameId).getDeviceId();
  }
}
// eslint-disable-next-line dot-notation
window['ABetterChoice'] = ABetterChoice;
module.exports = ABetterChoice;
