import { Config, DefaultLogger, MainSDK } from '@abetterchoice/abc_web_core';
import { isByteDancePlatform, isNativePlatform } from './cc_tt_utils';
import { CcTtEventComponent } from './event/cc_tt_event_component';
import { CcTtExpComponent } from './experiment/cc_tt_exp_component';
import { CcTtFeatureFlagComponent } from './featureflag/cc_tt_feature_flag_component';

export class ABetterChoiceAPIForNative extends MainSDK {
  constructor(config: Config) {
    super(config);
  }
  initEventImpl(): Promise<boolean> {
    if (isNativePlatform() || !isByteDancePlatform()) {
      DefaultLogger.debug('not WechatPlatform is not supported');
      return Promise.resolve(false);
    }
    this.eventComponent = new CcTtEventComponent(this.config);
    return this.eventComponent.initEventData();
  }

  initExperimentImpl(): Promise<boolean> {
    if (isNativePlatform() || !isByteDancePlatform()) {
      DefaultLogger.debug('not WechatPlatform is not supported');
      return Promise.resolve(false);
    }
    this.expComponent = new CcTtExpComponent(this.config);
    return this.expComponent.initExpData();
  }

  initFeatureFlagImpl(): Promise<boolean> {
    if (isNativePlatform() || !isByteDancePlatform()) {
      DefaultLogger.debug('not WechatPlatform is not supported');
      return Promise.resolve(false);
    }
    this.featureFlagComponent = new CcTtFeatureFlagComponent(this.config);
    return this.featureFlagComponent.initFeatureFlagData();
  }
}
// eslint-disable-next-line dot-notation
window['ABetterChoiceAPI'] = ABetterChoiceAPIForNative;
