import { UUID_STROAGE_KEY } from '@abetterchoice/abc_web_core';

// @ts-ignore
const env = cc;

export function isArrayBuffer(obj) {
  return Object.prototype.toString.call(obj) === '[object ArrayBuffer]';
}

export function postTtReq(
  url: string,
  data: Record<string, unknown>,
  headers: Record<string, string> = {},
  httpTimeout?: number,
): Promise<string> {
  return new Promise((resolve, reject) => {
    const reqObj: any = {
      url,
      data,
      header: { 'Content-Type': 'application/json;charset=UTF-8', ...headers },
      method: 'POST',
      success(res) {
        if (res.statusCode === 200) {
          resolve(res.data);
        } else {
          reject(new Error(`error http code ${res.statusCode}`));
        }
      },
      fail() {
        reject(new Error('error load net data'));
      },
    };
    if (httpTimeout && httpTimeout > 0) {
      reqObj.timeout = httpTimeout;
    }
    // @ts-ignore
    tt.request(reqObj);
  });
}

export function getTtDeviceId(prefix: string = '') {
  // @ts-ignore
  return tt.getStorageSync(prefix + UUID_STROAGE_KEY) || '';
}

export function isIOS() {
  if (env.sys.isNative && env.sys.os === 'iOS') {
    return true;
  }
  return false;
}

export function isAndroid() {
  if (env.sys.isNative && env.sys.os === 'Android') {
    return true;
  }
  return false;
}

// Check if the platform is native
export function isNativePlatform() {
  if (isIOS() || isAndroid()) {
    return true;
  }
  return false;
}

export function isByteDancePlatform() {
  if (env.sys.platform === 117 || env.sys.platform === env.sys.Platform.BYTEDANCE_MINI_GAME) {
    return true;
  }
  return false;
}
