import { Config, ExpDataStorage } from '@abetterchoice/abc_web_core';
import { isByteDancePlatform, isNativePlatform } from '../cc_tt_utils';

/**
 * @file cc_tt_storage.ts
 * @author fitcher
 * @date 2024-09-30
 */
export class CcTtExpDataStorage extends ExpDataStorage {
  constructor(config: Config) {
    super(config);
  }
  protected getStorageAllKey(): Promise<any | null> {
    if (isNativePlatform() || !isByteDancePlatform()) {
      return Promise.reject(null);
    }
    return new Promise(((resolve) => {
      // @ts-ignore
      // eslint-disable-next-line no-undef
      tt.getStorageInfo({
        success(res) {
          resolve(res.keys);
        },
        fail() {
          resolve(null);
        },
      });
    }));
  }

  protected getStorageString(key: string): Promise<string | null> {
    if (isNativePlatform() || !isByteDancePlatform()) {
      return Promise.reject(null);
    }
    return new Promise((resolve) => {
      // @ts-ignore
      // eslint-disable-next-line no-undef
      tt.getStorage({
        key,
        success(res) {
          resolve(res.data);
        },
        fail() {
          resolve(null);
        },
      });
    });
  }

  protected putStorageString(key: string, data: string): Promise<void> {
    if (isNativePlatform() || !isByteDancePlatform()) {
      return Promise.resolve();
    }
    return new Promise((resolve) => {
      // @ts-ignore
      // eslint-disable-next-line no-undef
      tt.setStorage({
        key,
        data,
      });
      resolve();
    });
  }

  protected removeStorageString(key: string): Promise<void | null> {
    if (!isNativePlatform() || !isByteDancePlatform()) {
      return Promise.resolve(null);
    }
    return new Promise((resolve) => {
      // @ts-ignore
      // eslint-disable-next-line no-undef
      tt.removeStorage({
        key,
        success(res) {
          resolve();
        },
        fail() {
          resolve(null);
        },
      });
    });
  }
}
